import React, { useEffect, useState } from "react";
import {
  Button,
  Counter,
  FormLayout,
  Input,
  Panel,
  PanelHeader,
  PanelHeaderBack,
} from "@vkontakte/vkui";

const Payment = ({ id, goBack }) => {
  const [date, setDate] = useState("");
  const [signature, setSignature] = useState("");
  const [visible, setVisible] = useState(false);
  useEffect(() => {
    const date = Date.now();
    setDate(date);
    const data = {
      testing: "1",
      salt: "dPUTLtbMfcTGzkaBnGtseKlcQymCLrYI",
      order_id: String(Math.round(date / 1000)),
      custom_order_id: String(Math.round(date / 1000)),
      amount: "1",
      merchant: "57170613-71d8-444e-8b69-8253749eee13",
      description: `Заказ №${String(Math.round(date / 1000))}`,
      success_url: "https://pay.modulbank.ru/success",
      unix_timestamp: String(Math.round(date / 1000)),
    };
    fetch("https://sahpossum.herokuapp.com/pay/sign", {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, cors, *same-origin
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "same-origin", // include, *same-origin, omit
      headers: {
        "Content-Type": "application/json",
      },
      redirect: "follow", // manual, *follow, error
      referrer: "no-referrer", // no-referrer, *client
      body: JSON.stringify(data), // тип данны
    })
      .then((res) => res.json())
      .then((res) => {
        console.log(res);
        setSignature(res);
      });
  }, []);
  return (
    <Panel id={id}>
      <PanelHeader left={<PanelHeaderBack onClick={() => goBack} />} />
      <form target="payFrame" method="post" action="https://pay.modulbank.ru/pay">
        <input type="hidden" name="testing" value="1" />
        <input
          type="hidden"
          name="salt"
          value="dPUTLtbMfcTGzkaBnGtseKlcQymCLrYI"
        />
        <input
          type="hidden"
          name="order_id"
          value={String(Math.round(date / 1000))}
        />
        <input
          type="hidden"
          name="custom_order_id"
          value={String(Math.round(date / 1000))}
        />
        <input type="hidden" name="amount" value="1" />
        <input
          type="hidden"
          name="merchant"
          value="57170613-71d8-444e-8b69-8253749eee13"
        />
        <input type="hidden" name="signature" value={String(signature)} />+
        <input
          type="hidden"
          name="description"
          value={`Заказ №${String(Math.round(date / 1000))}`}
        />
        <input
          type="hidden"
          name="success_url"
          value="https://pay.modulbank.ru/success"
        />
        <input
          type="hidden"
          name="unix_timestamp"
          value={Math.round(Date.now() / 1000)}
        />
        {!visible && (
          <Button
            type="submit"
            mode={"outline"}
            size={"xl"}
            //onClick={() => setVisible(true)}
          >
            Оплатить банковской картой
          </Button>
        )}
      </form>
      <iframe
        id={"payFrame"}
        name="payFrame"
        style={{
          // display: visible ? "block" : "none",
          width: "100%",
          height: 400
        }}
      />
    </Panel>
  );
};

export default Payment;
